import Vue from 'vue'
import App from './App.vue'
import BootstrapVue from 'bootstrap-vue'
import { createRouter } from './router'

Vue.config.productionTip = false
Vue.use(BootstrapVue)

export function createApp() {
    const router = createRouter()
    new Vue({
        router,
        render: h => h(App)
    }).$mount('#app')
}

createApp()
